.step-container {
  display: flex;
  //   background-color: yellow;

  .step-item {
    width: 20%;
    height: 65px;
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    border-bottom: 5px solid #cccccc;

    &.done {
      border-bottom: 5px solid #5cb85c;
    }

    &.hover {
      cursor: pointer;
    }

    .step-title {
      font-weight: 500;
      text-align: center;
    }

    .step-circle {
      width: 21px;
      height: 21px;
      padding-top: 1px;
      position: absolute;
      display: grid;
      place-items: center;
      background-color: #cccccc;
      bottom: -10px;
      border-radius: 50%;

      &.done {
        background-color: #5cb85c;
      }

      i {
        font-size: 19px;
        color: white;
      }
    }
  }
}
